*{
  margin: 0;
  padding: 0;
}
body{
  height:100vh;
  background-color: rgb(140, 12, 129);
}
.feedback__container{
  display: flex;
  align-items: center;
  flex-direction: column;
}
.feedback__container>h1{
  color: rgb(52, 3, 3);
  font-size: 3rem;
  margin: 2rem;
  border-bottom: 2px solid rgb(56, 3, 3);
  padding: 1rem;
}
label{
  font-size: 1.3rem;
  color: rgb(0, 0, 0);
  padding-right: 3rem;
}
.feedback__container form{
  display: flex;
  height: 25vh;
  flex-direction: column;
  gap: 1.8rem;
  padding: 2rem;
}
.input__container{
  display: flex;
  justify-content: space-between;
}
.input__container input{
  width: 15rem;
  height: 2rem;
  padding: 0.20rem;
}
.submitbtn{
  width: 70%;
  padding: 0.5rem 1rem;
  font-weight: 1000;
  margin:auto;
  background-color: rgb(41, 41, 232);
  color: white;
  cursor: pointer;
  border-radius: 10px;
  margin-top: 50px;
  border: 2px solid rgb(239, 40, 40);
}
.submitbtn:hover{
  color: white;
  background-color: rgb(232, 57, 57);
}
.submit__container{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-around;
  background-color: rgb(91, 245, 112);
  margin-top: 7rem;
  min-height: 30vh;
}
.item__data{
  display: flex;
  background-color: rgb(0, 198, 191);
  font-size: 1.1rem;
  font-weight: 1000;
  border: 3px solid black;
  margin: 1rem 0;
  height: 3rem;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
  border-radius: 25px;
}
.item__data p{
  padding: 1rem;
  font-family: 'Roboto',sans-serif;
}
.submit__item{
  min-width: 615px;
}